export default {
    general: {
        chart: {
            spacingLeft: 0,
            spacingRight: 0,
            style: {
                fontFamily: '\'Roboto\', sans-serif',
            },
        },
        exporting: {
            buttons: {
                contextButton: {
                    symbol: 'ellipsis',
                    symbolSize: 16,
                },
            },
        },
        legend: {
            itemMarginBottom: 15,
            itemStyle: {
                fontSize: '.75rem',
                fontWeight: 'normal',
            },
            padding: 0,
            margin: 30,
            squareSymbol: true,
            symbolHeight: 12,
            symbolWidth: 12,
            symbolRadius: 6,
        },
        plotOptions: {
            bar: {
                borderColor: null,
            },
            column: {
                borderColor: null,
            },
        },
        title: {
            align: 'left',
            margin: 20,
            style: {
                color: '#303436',
                fontSize: '1rem',
                fontWeight: '600',
            },
            x: 10,
        },
        subtitle: {
            align: 'left',
            style: {
                color: '#7a8085',
                fontSize: '.875rem',
            },
            x: 10,
        },
        tooltip: {
            backgroundColor: '#ffffff',
            borderColor: '#e4e6e7',
            headerFormat: '<span style="font-weight: 500; ">{point.key}</span><br/>',
            pointFormat: '<span style="color:{point.color}; font-size: 18px; ">●    </span>{series.name}: <span>{point.y}</span><br/>',
            followPointer: false,
            style: {
                fontSize: '.875rem',
            },
            padding: 16
        },
        xAxis: {
            labels: {
                style: {
                    color: '#303436',
                    fontSize: '.875rem',
                },
            },
            title: {
                margin: 20,
                style: {
                    color: '#303436',
                }
            }
        },
        yAxis: {
            labels: {
                style: {
                    color: '#303436',
                    fontSize: '.875rem',
                },
            },
            title: {
                margin: 20,
                style: {
                    color: '#303436',
                }
            }
        },
        colors: [
            '#b9e6ec',
            '#6dc2cd',
            '#3d94c0',
            '#2a5599',
            '#131c57',
        ],
    },
    themes: {
        blue: {
            colors: [
                '#b9e6ec',
                '#6dc2cd',
                '#3d94c0',
                '#2a5599',
                '#131c57',
            ],
        },
        orange: {
            colors: [
                '#fdd0a2',
                '#fd8d3c',
                '#d94901',
                '#7f2704',
                '#673223',
            ],
        },
        green: {
            colors: [
                '#cdff9f',
                '#76cb7e',
                '#319a89',
                '#2d6371',
                '#2e3041',
            ],
        },
        pink: {
            colors: [
                '#ebdefc',
                '#dea7db',
                '#e7298a',
                '#a80f52',
                '#5c0620',
            ],
        },
        red: {
            colors: [
                '#fee0d2',
                '#ff9b7a',
                '#ef3a2d',
                '#a50f15',
                '#500009',
            ],
        },
        yellow: {
            colors: [
                '#f5ef8f',
                '#fece22',
                '#e29d00',
                '#80581a',
                '#422908',
            ],
        }
    },
    types: {
        donut: {
            chart: {
                type: 'pie',
            },
            plotOptions: {
                pie: {
                    borderWidth: 0.5,
                    dataLabels: {
                        enabled: false,
                    },
                    innerSize: '70%',
                    showInLegend: true,
                    states: {
                        hover: false,
                    },
                },
            },
            legend: {
                layout: 'horizontal',
                enabled: true,
                align: 'center',
                verticalAlign: 'bottom',
                itemDistance: 20,
                margin: 30
            },
        },
        pie: {
            chart: {
                type: 'pie',
            },
            plotOptions: {
                pie: {
                    borderWidth: 0.5,
                    dataLabels: {
                        enabled: false,
                    },
                    showInLegend: true,
                    states: {
                        hover: false,
                    },
                },
            },
            legend: {
                layout: 'horizontal',
                enabled: true,
                align: 'center',
                verticalAlign: 'bottom',
                itemDistance: 20,
                margin: 30
            },
        },
        column: {
            chart: {
                type: 'column',
            },
            legend: {
                layout: 'horizontal',
                enabled: true,
                align: 'center',
                verticalAlign: 'bottom',
                itemDistance: 20,
                margin: 30
            },
        },
        bar: {
            chart: {
                type: 'bar',
                spacingRight: 1,
                tickLength: 0,
            },
            title: {
                x: 0,
            },
            subtitle: {
                x: 0,
            },
            legend: {
                layout: 'horizontal',
                enabled: true,
                align: 'center',
                verticalAlign: 'bottom',
                itemDistance: 20,
                margin: 30
            },
            plotOptions: {
                column: {
                    pointPadding: 0.1,
                    groupPadding: 0.1,
                },
            },
        },
        spline: {
            chart: {
                type: 'spline',
            },
            plotOptions: {
                spline: {
                    marker: {
                        enabled: false
                    }
                },
                series: {
                    stacking: 'normal',
                },
            },
        },
        basicLine: {
            chart: {
                type: 'line',
            },
        },
        stackedArea: {
            chart: {
                type: 'area',
            },
            plotOptions: {
                area: {
                    stacking: 'normal',
                    marker: {
                        enabled: false,
                    },
                    lineWidth: 0
                },
            },
        },
        bubble: {
            chart: {
                type: 'bubble',
            },
            tooltip: {
                backgroundColor: '#ffffff',
                borderColor: '#e4e6e7',
                headerFormat: '<span style="color:{point.color}; font-size: 18px; ">●    </span><span style="font-weight: 500; ">{series.name}</span><br/>',
                pointFormat: '<span>({point.x}, {point.y}), Size: {point.z}</span><br/>',
                followPointer: false,
                style: {
                    fontSize: '.875rem',
                },
                padding: 16
            },
            plotOptions: {
                bubble: {
                    marker: {
                        fillOpacity: 0.75,
                        lineWidth: 0,
                    },
                },
            },
        },
    },
};
